import { createBrowserHistory } from "history";

const isBuild: boolean = true;
export const browserHistory = createBrowserHistory();
export const serverPort = isBuild ? 80 : 8000;
export const serverBaseURL = `http://${window.location.hostname}:${serverPort}`;
export const appTitle: string = "Rail Transport";
export const serverUrl: string = `http://${window.location.hostname}${
  isBuild ? "" : `:${serverPort}`
}/gql`;
